import Carousel from 'react-bootstrap/Carousel';

function HomePageCarousel() {
    return (
        <header>
            <Carousel fade>
                <Carousel.Item className="carousel-image" style={{ backgroundImage: `url("/static/images/car-1.jpg")` }}>
                    <Carousel.Caption className="d-flex align-items-center">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12 px-5">
                                    <h1 className="text-yellow animated fadeInDown py-3">GRADNJA AVM</h1>
                                    <h4 className="text-white text-uppercase animated fadeInUp delay-1s">Najpouzdaniji i najiskusniji</h4>
                                </div>
                            </div>
                        </div>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item className="carousel-image" style={{ backgroundImage: `url("/static/images/car-2.jpg")` }}>
                    <Carousel.Caption className="d-flex align-items-center">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-6 px-5">
                                    <div class="carousel-content-wrap">
                                        <div class="carousel-content-wrap-head bg-yellow"></div>
                                        <div class="service_slid_item text-center">
                                            <div class="service_slid_text">
                                                <span class="icon icon icon-tools text-black"></span>
                                                <h5 class="text-black m-top-20">UI/UX Design</h5>
                                            </div>
                                            <div class="service_slid_text">
                                                <span class="icon icon icon-sports-2 text-black"></span>
                                                <h5 class="text-black m-top-20">UI/UX Design</h5>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item className="carousel-image" style={{ backgroundImage: `url("/static/images/car-3.jpg")` }}>
                    <Carousel.Caption className="d-flex align-items-center">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12 px-5">
                                    <h1 className="animated fadeInDown">Third slide label</h1>
                                    <p className="animated fadeInUp delay-1s">Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                                </div>
                            </div>
                        </div>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </header>
    )
}

export default HomePageCarousel;